<template>
    <div
        id="products-container"
        class="products-container container-fluid"
        v-bind:class="{ 'fix-products': scrollPosition > 0 }"
    >
        <div class="container">
               <div class="nav-menu-btn advanced_filter_button d-sm-none" style="padding:8px" @click="hide_or_display_filter()">
                                    <font-awesome-icon
                                        @click="active = !active"
                                        :icon="['fas', 'bars']"
                                    /> Advanced Filter
                                </div>
            <div class="products-filter-container col-sm-12 col-md-3 d-none d-md-block" id="advanced_filter">
             
                <div class="products-filter">
                    <div class="product-colors col-12">
                        <div class="product-colors-head head">
                            Choose Color
                        </div>
                        <div class="product-colors-sec">
                            <ul>
                                <li
                                    v-for="color in colors"
                                    :key="color.num"
                                    :class="color.state"
                                    @click="filter()"
                                >
                                    <span
                                        :data-color="color.color"
                                        @click="selectcolor($event, color.num)"
                                        :style="{ '--pro-color':color.color }"
                                    >
                                    </span>
                                    <div class="check-icon">
                                        <font-awesome-icon
                                            :icon="['fas', 'check']"
                                        />
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>

                    <div class="sort-by-category col-12">
                        <div class="sort-by-category-head head">
                            sort by category
                        </div>
                        <div class="sort-category">
                            <ul>
                                <li v-for="(brand, index) in categories" :key="brand">
                                    {{brand}}
                                    <input type="checkbox" name="brand" v-model="checked[index]" @change="filter()"/>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div class="products-items-container col-lg-9 col-12">
                <div class="filter-container">
                    <div class="slide-filter">
                        <span>Quick Filter </span>
                        <font-awesome-icon :icon="['fas', 'ellipsis-v']" @click="slideFilter" />
                    </div>
                    <ul :style="{ '--height': filterListHeight }" @click="slideFilter">
                        <li @click="filt('customer_rating','ASC')">top rate</li>
                        <li @click="filt('price','ASC')">lowest price</li>
                    </ul>
                </div>
                <div class="items">
                    <Item
                        v-for="item in items"
                        v-bind:key="item.id"
                        :id="item.id"
                        :views="item.no_viewed"
                        :productname="item.name"
                        :category="item.category"
                        :sale="item.discount"
                        :oldprice="item.oldprice"
                        :currentprice="item.selling_price"
                        :currentrating="item.currentrating"
                        :rate="item.rate"
                        :img="item.photo_link"
                        :quantity="item.quantity"
                        :size="item.size"
                        :color="item.color"
                    />
                </div>
                <div class="overflow-auto">
                    <b-pagination-nav :link-gen="link" :number-of-pages="pagination" v-model="currentPage" use-router></b-pagination-nav>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss">
@import "../style/css/scss/views/_products.scss";
</style>
<script>
import axios from 'axios';
import Item from "../components/global/item.vue";

export default {
    components: {
        Item
    },

    data() {
        return {
            scrollPosition: null,
            items: this.$store.state.items, 
            filterListHeight: "0%",
            colors: [],
            selectedColor: "",
            fillter:"",
            brands:[],
            categories:[],
            checked:[],
            currentPage:1,
            pagination:"8",
            bool:false
        };
    },
    watch:{
        currentPage: function(){
            axios.get(
                ("https://queen-store.net/elsedeek/public_html/board/public_html/api/items?page="+this.currentPage),
            ).then((res)=>{
                this.items = res.data.data;
            })
        },
        '$route.query.search':function(){
            axios.post(
                ('https://queen-store.net/elsedeek/public_html/board/public_html/api/items_search'),
                {
                    value:this.$route.query.search
                }
                ).then((res) => {
                    this.items = res.data.data
                })
        }
    },
    methods: {
        updateScroll() {
            this.scrollPosition = window.scrollY;
        },
        selectcolor: function(event, index) {

            
            for (var i = 0; i < this.colors.length; i++) {
                
                if (i == index) {
                    this.colors[i].state = "active";
                    this.selectedColor = event.target.getAttribute(
                        "data-color"
                    );
                } else {
                    this.colors[i].state = "not-active";
                }
                    

            }

        },
        link(r){
            return r;
        },
        slideFilter: function() {
            if (this.filterListHeight == "fit-content") {
                this.filterListHeight = "0%";
            } else {
                this.filterListHeight = "fit-content";
            }
        },
        hide_or_display_filter: function(){
            var x = document.getElementById('advanced_filter')
            if (x.classList.contains('d-none')) {
                 x.classList.remove('d-none')

            } else {
                 x.classList.add('d-none')

            }


        },
        filter(){
            this.bool=false;
            this.items = []
            if(this.selectedColor != ""){
                    axios.post(
                        ("https://queen-store.net/elsedeek/public_html/board/public_html/api/filter"),
                        {
                            direction: 'ASC',
                            sort_by:'price',
                            color:this.selectedColor
                        }            
                        ).then((res) => {
                            this.items = []
                            for(var i = 0 ; i < res.data.data.length ; i++){
                                this.items.push(res.data.data[i]);
                            }
                        });
            }
            for(var i = 0 ; i < this.checked.length ; i++){
                if(this.checked[i] == true){
                    this.bool=true;
                    axios.post(
                        ("https://queen-store.net/elsedeek/public_html/board/public_html/api/filter"),
                        {
                            direction: 'ASC',
                            sort_by:'price',
                            color:this.selectedColor,
                            category:this.categories[i]
                        }            
                        ).then((res) => {
                            for(var x = 0 ; x < res.data.data.length ; x++){
                                this.items.push(res.data.data[x]);
                            }
                        });
                }
            }

            if(this.bool==false){
            axios.get(
            "https://queen-store.net/elsedeek/public_html/board/public_html/api/items",
            {
                params:{
                    category:this.$route.query.cat
                }
            }
            ).then((res)=>{
                this.items = res.data.data;
            })
        }
        },
        filt(state,dir){
            axios.post(
                        ("https://queen-store.net/elsedeek/public_html/board/public_html/api/filter"),
                        {
                            direction: dir,
                            sort_by:state,
                        }            
                        ).then((res) => {
                            this.items = []
                            for(var i = 0 ; i < res.data.data.length ; i++){
                                this.items.push(res.data.data[i]);
                            }
						this.pagination = res.data.last_page;

                        });
        }
    },
    mounted() {

        window.addEventListener("scroll", this.updateScroll);
        axios.get(
            ("https://queen-store.net/elsedeek/public_html/board/public_html/api/colors"),
        ).then((res) => {
            for(var i = 0 ; i < res.data.length ; i++){
                this.colors.push({num: i , color: res.data[i] , state: 'not-active'}) ;
            }
        });
        axios.get(
            ("https://queen-store.net/elsedeek/public_html/board/public_html/api/brands"),
        ).then((res) => {
            this.brands = res.data;
        });
        axios.get(
            ("https://queen-store.net/elsedeek/public_html/board/public_html/api/getcategories"),
        ).then((res) => {
            this.categories = res.data;
        });
        if(this.$route.query.cat){
            axios.get(
            "https://queen-store.net/elsedeek/public_html/board/public_html/api/items",
            {
                params:{
                    category:this.$route.query.cat
                }
            }
            ).then((res)=>{
                this.items = res.data.data;
            })
        }else if(this.$route.query.search){
            axios.post(
                ('https://queen-store.net/elsedeek/public_html/board/public_html/api/items_search'),
                {
                    value:this.$route.query.search
                }
                ).then((res) => {
                    this.items = res.data.data
                })
        }
        else{
            axios.get(
            ("https://queen-store.net/elsedeek/public_html/board/public_html/api/items?page="+this.currentPage),
            ).then((res)=>{
                this.items = res.data.data;
            })
        }
        












        axios.get(
            "https://queen-store.net/elsedeek/public_html/board/public_html/api/items",
            ).then((res)=>{
                this.pagination = res.data.last_page;
            })
    }
};


</script>
